export function createStore(args) {
    const { effectsHandler, initialState, setState, update } = args;
    let state = initialState;
    function handleMsg(msg) {
        const [nextState, effect] = update(state, msg);
        state = nextState;
        effectsHandler.value(effect);
        setState(state);
    }
    return {
        send: handleMsg,
        getState: () => state,
    };
}
