// Very basic and efficient immutable stack
export class Empty {
    constructor() { }
    static make() {
        return Empty.instance;
    }
    isEmpty() {
        return true;
    }
    isNonEmpty() {
        return false;
    }
    cons(value) {
        return NonEmpty.make(value, this);
    }
    foldl(_, initial) {
        return initial;
    }
    reverse() {
        return this;
    }
    toArray() {
        return [];
    }
    toString() {
        return "Nil";
    }
}
Empty.instance = new Empty();
export class NonEmpty {
    constructor(head, tail) {
        this.head = head;
        this.tail = tail;
    }
    static of(value) {
        return new NonEmpty(value, Empty.make());
    }
    static make(head, tail) {
        return new NonEmpty(head, tail);
    }
    isEmpty() {
        return false;
    }
    isNonEmpty() {
        return true;
    }
    cons(value) {
        return new NonEmpty(value, this);
    }
    foldl(f, initial) {
        let stack = this;
        let state = initial;
        while (stack.isNonEmpty()) {
            state = f(state, stack.head);
            stack = stack.tail;
        }
        return state;
    }
    reverse() {
        let nonReversed = this.tail;
        let reversed = NonEmpty.of(this.head);
        while (nonReversed.isNonEmpty()) {
            reversed = reversed.cons(nonReversed.head);
            nonReversed = nonReversed.tail;
        }
        return reversed;
    }
    toArray() {
        const arr = [this.head];
        let stack = this.tail;
        while (stack.isNonEmpty()) {
            arr.push(stack.head);
            stack = stack.tail;
        }
        return arr;
    }
    toString() {
        return "Cons (" + this.head + " " + this.tail + ")";
    }
}
export function fromArray(arr) {
    let stack = Empty.make();
    for (let i = 0; i < arr.length; i++) {
        stack = stack.cons(arr[i]);
    }
    return stack;
}
