import * as React from "react";
import styled from "../styled";
import * as Brush from "../tools/brush";
import * as Eraser from "../tools/eraser";
import { SinkableButton } from "../components/buttons";
import { SurfaceLook } from "../components/surface";
const LeftBar = styled(SurfaceLook) `
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    margin-right: 0.5rem;

    > :not(:last-child) {
        margin-bottom: 0.125rem;
    }
    > :not(:last-child) {
        margin-bottom: 0.125rem;
    }
`;
const ToolBarContainer = styled.div `
    display: flex;
`;
export function View(props) {
    const currentToolType = props.tool.current.type;
    const setTool = props.msgSender.setTool;
    return (<ToolBarContainer>
            <LeftBar>
                <SinkableButton dataKey="brush" onClick={() => setTool(0 /* Brush */)} isDown={currentToolType === 0 /* Brush */}>
                    
                </SinkableButton>
                <SinkableButton dataKey="erase" onClick={() => setTool(1 /* Eraser */)} isDown={currentToolType === 1 /* Eraser */}>
                    
                </SinkableButton>
                <SinkableButton dataKey="zoom" onClick={() => setTool(3 /* Zoom */)} isDown={currentToolType === 3 /* Zoom */}>
                    
                </SinkableButton>
            </LeftBar>
            {props.transientState.isDetailsExpanded ? ((() => {
        switch (currentToolType) {
            case 0 /* Brush */:
                return (<Brush.Details messageSender={props.msgSender.brush} tool={props.tool.brush}/>);
            case 1 /* Eraser */:
                return (<Eraser.Details messageSender={props.msgSender.eraser} tool={props.tool.eraser}/>);
            default:
                return <></>;
        }
    })()) : (<></>)}
        </ToolBarContainer>);
}
