import { Vec2 } from "../util";
export function init() {
    return {
        zoomPct: 1,
        offsetPx: new Vec2(0, 0),
        rotationRad: 0,
    };
}
export function update(state, msg) {
    switch (msg.type) {
        case 0 /* SetZoom */:
            return { ...state, zoomPct: msg.payload };
        case 1 /* SetOffset */:
            return { ...state, offsetPx: msg.payload };
        case 2 /* SetRotation */:
            return { ...state, rotationRad: msg.payload };
    }
}
export function createSender(sendMessage) {
    return {
        setRotation: pct => sendMessage({ type: 2 /* SetRotation */, payload: pct }),
        setOffset: xyPct => sendMessage({ type: 1 /* SetOffset */, payload: xyPct }),
        setZoom: pct => sendMessage({ type: 0 /* SetZoom */, payload: pct }),
    };
}
export function zoomToolUpdate(camera, dragState, input) {
    const xd = input.x - dragState.prevPoint.x;
    const zoomPct = camera.zoomPct + xd / 150;
    return { type: 0 /* SetZoom */, payload: zoomPct };
}
export function rotateToolUpdate(_camera, dragState, input) {
    const rotationRad = Math.atan2(input.y - dragState.clickPoint.y, input.x - dragState.clickPoint.x);
    return { type: 2 /* SetRotation */, payload: rotationRad };
}
export function moveToolUpdate(camera, dragState, input) {
    const xd = input.x - dragState.prevPoint.x;
    const yd = input.y - dragState.prevPoint.y;
    const offset = new Vec2(camera.offsetPx.x + xd, camera.offsetPx.y + yd);
    return { type: 1 /* SetOffset */, payload: offset };
}
