import * as React from "react";
import styled from "../styled";
import * as Interp from "./interpolation";
import * as BrushDelay from "./brushDelay";
import * as Color from "../color";
import { Vec2, colorModeToString } from "../util";
import { ZipperList } from "../zipperList";
import { Menu } from "../components/menu";
import { ColorWheel } from "../components/colorWheel";
import { Labeled } from "../components/labeled";
import { Slider } from "../components/slider";
import { InlineLabeled } from "../components/inlineLabeled";
import { Switch } from "../components/switch";
import { ColorDisplay } from "../components/colorDisplay";
import { Surface } from "../components/surface";
export function createBrushSender(sendMessage) {
    return {
        setColor: color => sendMessage({ type: 3 /* SetColor */, payload: color }),
        setColorMode: mode => sendMessage({ type: 4 /* SetColorMode */, payload: mode }),
        setDelay: ms => sendMessage({ type: 9 /* SetDelay */, payload: ms }),
        setDiameter: px => sendMessage({ type: 0 /* SetDiameter */, payload: px }),
        setOpacity: pct => sendMessage({ type: 2 /* SetOpacity */, payload: pct }),
        setSoftness: pct => sendMessage({ type: 1 /* SetSoftness */, payload: pct }),
        setSpacing: px => sendMessage({ type: 5 /* SetSpacing */, payload: px }),
        setPressureAffectsOpacity: x => sendMessage({ type: 6 /* SetPressureAffectsOpacity */, payload: x }),
        setPressureAffectsSize: x => sendMessage({ type: 7 /* SetPressureAffectsSize */, payload: x }),
        swapColorFrom: () => sendMessage({ type: 8 /* SwapColor */, payload: undefined }),
    };
}
export function initTempState() {
    return null;
}
export function init() {
    return {
        diameterPx: 15,
        softness: 0.4,
        flowPct: 0.3,
        colorMode: ZipperList.fromArray([1 /* Hsluv */, 0 /* Hsv */]),
        color: new Color.Hsluv(73, 100, 16),
        colorSecondary: new Color.Hsluv(0, 0, 100),
        spacingPct: 0.05,
        pressureAffectsOpacity: false,
        pressureAffectsSize: true,
        delay: BrushDelay.delay(5),
    };
}
export function update(state, msg) {
    switch (msg.type) {
        case 0 /* SetDiameter */:
            return { ...state, diameterPx: msg.payload };
        case 1 /* SetSoftness */:
            return { ...state, softness: msg.payload };
        case 2 /* SetOpacity */:
            return { ...state, flowPct: msg.payload };
        case 3 /* SetColor */: {
            return { ...state, color: msg.payload };
        }
        case 4 /* SetColorMode */: {
            console.log("changing color mode to", msg.payload);
            return { ...state, colorMode: state.colorMode.focusf(x => x === msg.payload) };
        }
        case 5 /* SetSpacing */:
            return { ...state, spacingPct: msg.payload };
        case 6 /* SetPressureAffectsOpacity */:
            return { ...state, pressureAffectsOpacity: msg.payload };
        case 7 /* SetPressureAffectsSize */:
            return { ...state, pressureAffectsSize: msg.payload };
        case 8 /* SwapColor */: {
            return {
                ...state,
                color: state.colorSecondary,
                colorSecondary: state.color,
            };
        }
        case 9 /* SetDelay */:
            return { ...state, delay: BrushDelay.delay(msg.payload) };
    }
}
export function onClick(camera, brush, input) {
    const brushInput = pointerToBrushInput(camera, input);
    const interpState = Interp.init(createInputPoint(brush, brushInput));
    const delayState = BrushDelay.init(input.time, brushInput);
    return [{ interpState, delayState }, createBrushPoint(brush, brushInput)];
}
export function onDrag(camera, brush, state, input) {
    if (state === null) {
        const res = onClick(camera, brush, input);
        return [res[0], [res[1]]];
    }
    const brushInput = pointerToBrushInput(camera, input);
    const [delayState, newBrushInput] = BrushDelay.updateWithInput(brush.delay, state.delayState, input.time, brushInput);
    const [interpState, brushPoints] = Interp.interpolate(brush, state.interpState, createInputPoint(brush, newBrushInput));
    return [{ delayState, interpState }, brushPoints];
}
export function onFrame(brush, state, currentTime) {
    if (state === null)
        return [null, []];
    if (brush.delay.duration <= 0)
        return [null, []];
    const [delayState, newBrushInput] = BrushDelay.update(brush.delay, state.delayState, currentTime);
    const [interpState, brushPoints] = Interp.interpolate(brush, state.interpState, createInputPoint(brush, newBrushInput));
    return [{ delayState, interpState }, brushPoints];
}
export function onRelease(camera, brush, state, input) {
    if (state === null)
        return [null, []];
    return [null, []];
}
function pointerToBrushInput(_camera, input) {
    // TODO: offset, zoom, and stuff
    return { x: input.x, y: input.y, pressure: input.pressure };
}
function createBrushPoint(brush, input) {
    const alpha = brush.flowPct * input.pressure;
    const color = alpha === 1
        ? brush.color.toRgb().toLinear()
        : brush.color
            .toRgb()
            .toLinear()
            .mix(1 - alpha, Color.RgbLinear.Black);
    const position = new Vec2(input.x, input.y);
    return {
        alpha,
        color,
        position,
        rotation: 0,
        scaledDiameter: brush.diameterPx * input.pressure,
    };
}
function createInputPoint(brush, input) {
    const alpha = brush.flowPct * input.pressure;
    const color = alpha === 1
        ? brush.color.toRgb().toLinear()
        : brush.color
            .toRgb()
            .toLinear()
            .mix(1 - alpha, Color.RgbLinear.Black);
    const position = new Vec2(input.x, input.y);
    return {
        alpha,
        color,
        position,
        pressure: input.pressure,
        rotation: 0,
    };
}
/*
 *
 * Views
 *
 */
const TextInput = styled.input `
    background-color: ${p => p.theme.color.surface.toStyle()};
    color: ${p => p.theme.color.onSurface.toStyle()};
    padding: 0.25rem 0;
    border-radius: 0.25rem;
    font-family: ${p => p.theme.fonts.monospace};

    &:focus {
        background-color: ${p => p.theme.color.secondary.toStyle()};
        color: ${p => p.theme.color.onSecondary.toStyle()};
        padding: 0.25rem 0.5rem;
    }
`;
const DetailsContainer = styled(Surface) `
    background-color: ${p => p.theme.color.surface.toStyle()};
    color: ${p => p.theme.color.onSurface.toStyle()};
    flex-direction: column;
    width: 12rem;
    padding: 0.5rem 0.75rem;
`;
export function Details(props) {
    const sender = props.messageSender;
    const brush = props.tool;
    const color = brush.color;
    return (<DetailsContainer>
            <div style={{ margin: "0.5rem 0" }}>
                <Menu choices={brush.colorMode} show={colorModeToString} onSelect={sender.setColorMode}/>
            </div>
            <ColorWheel color={brush.color} colorType={brush.colorMode.focus} onChange={sender.setColor}/>
            <div style={{ margin: "0.5rem 0", width: "100%" }}>
                <ColorDisplay color={brush.color} colorSecondary={brush.colorSecondary} onClick={() => sender.swapColorFrom(brush.color)}/>
            </div>
            <div style={{ margin: "0.5rem 0" }}>
                <TextInput type="text" value={brush.color.toStyle()} style={{ width: "100%" }} onChange={text => {
        const rgb = Color.Rgb.fromCss(text.target.value);
        if (rgb === null)
            return;
        sender.setColor(Color.rgbToHsluv(rgb));
    }}/>
            </div>
            <Labeled label="Size" value={brush.diameterPx.toFixed(1) + "px"}>
                <Slider percentage={brush.diameterPx / 500} onChange={pct => sender.setDiameter(pct * 500)}/>
            </Labeled>
            <Labeled label="Softness" value={brush.softness.toFixed(2)}>
                <Slider percentage={brush.softness} onChange={sender.setSoftness}/>
            </Labeled>
            <Labeled label="Flow" value={brush.flowPct.toFixed(2)}>
                <Slider percentage={brush.flowPct} onChange={sender.setOpacity}/>
            </Labeled>
            <Labeled label="Spacing" value={brush.spacingPct.toFixed(2) + "%"}>
                <Slider percentage={brush.spacingPct} onChange={sender.setSpacing}/>
            </Labeled>
            <ColorSliders sender={sender} color={color} colorType={brush.colorMode.focus}/>
            <InlineLabeled label="Pressure-Opacity">
                <Switch checked={brush.pressureAffectsOpacity} onCheck={sender.setPressureAffectsOpacity}/>
            </InlineLabeled>
            <InlineLabeled label="Pressure-Size">
                <Switch checked={brush.pressureAffectsSize} onCheck={sender.setPressureAffectsSize}/>
            </InlineLabeled>
            <Labeled label="Delay" value={brush.delay.duration.toFixed(0) + "ms"}>
                <Slider percentage={brush.delay.duration / 500} onChange={pct => sender.setDelay(pct * 500)}/>
            </Labeled>
        </DetailsContainer>);
}
function ColorSliders({ sender, color, colorType, }) {
    switch (colorType) {
        case 1 /* Hsluv */:
            return HsluvSliders(sender, color);
        case 0 /* Hsv */:
            return HsvSliders(sender, Color.rgbToHsv(color.toRgb()));
    }
}
function HsluvSliders(sender, color) {
    return (<>
            <Labeled label="Hue" value={color.h.toFixed(2)}>
                <Slider percentage={color.h / 360} onChange={pct => sender.setColor(color.withH(pct * 360))}/>
            </Labeled>
            <Labeled label="Saturation" value={color.s.toFixed(2)}>
                <Slider percentage={color.s / 100} onChange={pct => sender.setColor(color.withS(pct * 100))}/>
            </Labeled>
            <Labeled label="Luminosity" value={color.l.toFixed(2)}>
                <Slider percentage={color.l / 100} onChange={pct => sender.setColor(color.withL(pct * 100))}/>
            </Labeled>
        </>);
}
function HsvSliders(sender, color) {
    return (<>
            <Labeled label="Hue" value={color.h.toFixed(2)}>
                <Slider percentage={color.h} onChange={pct => sender.setColor(Color.rgbToHsluv(color.withH(pct).toRgb()))}/>
            </Labeled>
            <Labeled label="Saturation" value={color.s.toFixed(2)}>
                <Slider percentage={color.s} onChange={pct => sender.setColor(Color.rgbToHsluv(color.withS(pct).toRgb()))}/>
            </Labeled>
            <Labeled label="Value" value={color.v.toFixed(2)}>
                <Slider percentage={color.v} onChange={pct => sender.setColor(Color.rgbToHsluv(color.withV(pct).toRgb()))}/>
            </Labeled>
        </>);
}
