import * as Brush from "./brush";
import * as Eraser from "./eraser";
import * as Camera from "./camera";
export function createSender(sendMessage) {
    return {
        brush: Brush.createBrushSender(msg => sendMessage({ type: 1 /* BrushMsg */, payload: msg })),
        eraser: Eraser.createBrushSender(msg => sendMessage({ type: 2 /* EraserMsg */, payload: msg })),
        camera: Camera.createSender(msg => sendMessage({ type: 3 /* CameraMsg */, payload: msg })),
        setTool: type => sendMessage({ type: 0 /* SetTool */, payload: type }),
    };
}
export function init() {
    return {
        brush: Brush.init(),
        eraser: Eraser.init(),
        camera: Camera.init(),
        current: brushToolInit(),
    };
}
function brushToolInit() {
    return {
        type: 0 /* Brush */,
        state: Brush.initTempState(),
    };
}
function eraserToolInit() {
    return {
        type: 1 /* Eraser */,
        state: Eraser.initTempState(),
    };
}
function moveToolInit() {
    return {
        type: 3 /* Zoom */,
        state: null,
    };
}
function zoomToolInit() {
    return {
        type: 3 /* Zoom */,
        state: null,
    };
}
function rotateToolInit() {
    return {
        type: 3 /* Zoom */,
        state: null,
    };
}
export function update(tool, msg) {
    switch (msg.type) {
        case 0 /* SetTool */: {
            switch (msg.payload) {
                case 0 /* Brush */:
                    return { ...tool, current: brushToolInit() };
                case 1 /* Eraser */:
                    return { ...tool, current: eraserToolInit() };
                case 2 /* Move */:
                    return { ...tool, current: moveToolInit() };
                case 3 /* Zoom */:
                    return { ...tool, current: zoomToolInit() };
                case 4 /* Rotate */:
                    return { ...tool, current: rotateToolInit() };
                default:
                    throw "unexpected tool type: " + msg.payload;
            }
        }
        case 1 /* BrushMsg */:
            return { ...tool, brush: Brush.update(tool.brush, msg.payload) };
        case 2 /* EraserMsg */:
            return { ...tool, eraser: Eraser.update(tool.eraser, msg.payload) };
        case 3 /* CameraMsg */:
            return { ...tool, camera: Camera.update(tool.camera, msg.payload) };
    }
}
export function onClick(tool, pointer) {
    const { current } = tool;
    switch (current.type) {
        case 0 /* Brush */: {
            const [state, brushPoint] = Brush.onClick(tool.camera, tool.brush, pointer);
            return [{ ...tool, current: { type: 0 /* Brush */, state } }, [brushPoint]];
        }
        case 1 /* Eraser */: {
            const [state, brushPoint] = Eraser.onClick(tool.camera, tool.eraser, pointer);
            return [{ ...tool, current: { type: 1 /* Eraser */, state } }, [brushPoint]];
        }
        case 2 /* Move */: {
            if (current.state !== null)
                return [tool, []];
            const dragState = { clickPoint: pointer, prevPoint: pointer };
            const cameraMsg = Camera.moveToolUpdate(tool.camera, dragState, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 2 /* Move */, state: dragState } }, []];
        }
        case 3 /* Zoom */: {
            if (current.state !== null)
                return [tool, []];
            const dragState = { clickPoint: pointer, prevPoint: pointer };
            const cameraMsg = Camera.zoomToolUpdate(tool.camera, dragState, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 3 /* Zoom */, state: dragState } }, []];
        }
        case 4 /* Rotate */: {
            if (current.state !== null)
                return [tool, []];
            const dragState = { clickPoint: pointer, prevPoint: pointer };
            const cameraMsg = Camera.rotateToolUpdate(tool.camera, dragState, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 4 /* Rotate */, state: dragState } }, []];
        }
    }
}
export function onDrag(tool, pointer) {
    const { current } = tool;
    switch (current.type) {
        case 0 /* Brush */: {
            const [state, brushPoints] = Brush.onDrag(tool.camera, tool.brush, current.state, pointer);
            return [{ ...tool, current: { type: 0 /* Brush */, state } }, brushPoints];
        }
        case 1 /* Eraser */: {
            const [state, brushPoints] = Eraser.onDrag(tool.camera, tool.eraser, current.state, pointer);
            return [{ ...tool, current: { type: 1 /* Eraser */, state } }, brushPoints];
        }
        case 2 /* Move */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.moveToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            const dragState = {
                clickPoint: current.state.clickPoint,
                prevPoint: pointer,
            };
            return [{ ...tool, camera, current: { type: 2 /* Move */, state: dragState } }, []];
        }
        case 3 /* Zoom */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.zoomToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            const dragState = {
                clickPoint: current.state.clickPoint,
                prevPoint: pointer,
            };
            return [{ ...tool, camera, current: { type: 3 /* Zoom */, state: dragState } }, []];
        }
        case 4 /* Rotate */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.rotateToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            const dragState = {
                clickPoint: current.state.clickPoint,
                prevPoint: pointer,
            };
            return [{ ...tool, camera, current: { type: 4 /* Rotate */, state: dragState } }, []];
        }
    }
}
export function onRelease(tool, pointer) {
    const { current } = tool;
    switch (current.type) {
        case 0 /* Brush */: {
            const [state, brushPoints] = Brush.onRelease(tool.camera, tool.brush, current.state, pointer);
            return [{ ...tool, current: { type: 0 /* Brush */, state } }, brushPoints];
        }
        case 1 /* Eraser */: {
            const [state, brushPoints] = Eraser.onRelease(tool.camera, tool.eraser, current.state, pointer);
            return [{ ...tool, current: { type: 1 /* Eraser */, state } }, brushPoints];
        }
        case 2 /* Move */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.moveToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 2 /* Move */, state: null } }, []];
        }
        case 3 /* Zoom */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.zoomToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 3 /* Zoom */, state: null } }, []];
        }
        case 4 /* Rotate */: {
            if (current.state === null)
                return [tool, []];
            const cameraMsg = Camera.rotateToolUpdate(tool.camera, current.state, pointer);
            const camera = Camera.update(tool.camera, cameraMsg);
            return [{ ...tool, camera, current: { type: 4 /* Rotate */, state: null } }, []];
        }
    }
}
export function onFrame(tool, currentTime) {
    const { current } = tool;
    switch (current.type) {
        case 0 /* Brush */: {
            const [state, brushPoints] = Brush.onFrame(tool.brush, current.state, currentTime);
            if (state === current.state && brushPoints.length === 0) {
                return [tool, brushPoints];
            }
            else {
                return [{ ...tool, current: { type: 0 /* Brush */, state } }, brushPoints];
            }
        }
        case 1 /* Eraser */: {
            const [state, brushPoints] = Eraser.onFrame(tool.eraser, current.state, currentTime);
            if (state === current.state && brushPoints.length === 0) {
                return [tool, brushPoints];
            }
            else {
                return [{ ...tool, current: { type: 1 /* Eraser */, state } }, brushPoints];
            }
        }
        default:
            return [tool, []];
    }
}
export function getBlendMode(tool) {
    switch (tool.type) {
        case 0 /* Brush */:
            return 0 /* Normal */;
        case 1 /* Eraser */:
            return 1 /* Erase */;
        default:
            console.warn("Getting BlendMode for type " + tool.type + ", which is neithre a Brush or Eraser");
            return 0 /* Normal */;
    }
}
export function getSoftness(tool) {
    switch (tool.current.type) {
        case 0 /* Brush */:
            return tool.brush.softness;
        case 1 /* Eraser */:
            return tool.eraser.softness;
        default:
            console.warn("Getting BlendMode for type " +
                tool.current.type +
                ", which is neithre a Brush or Eraser");
            return 0;
    }
}
